import { fontSize } from "@mui/system";
import { useState } from "react";
import { FaStar } from "react-icons/fa";
import { Button,Text, Accordion,Box,
  AccordionItem,
  AccordionButton,
  AccordionPanel,
  AccordionIcon,useToast } from "@chakra-ui/react"

  import axios from "axios";
  import Cookies from 'js-cookie'

  const csrftoken = Cookies.get('csrftoken')
  
const colors = {
  orange: "#FFBA5A",
  grey: "#a9a9a9"

};

function Messages(props) {
  const toast = useToast()
  const [feed1, setfeed1] =useState('');
  const [starValue, setstarValue] = useState(0);
  const [currentValue, setCurrentValue] = useState(0);
  const [hoverValue, setHoverValue] = useState(undefined);
  const stars = Array(5).fill(0)

  const handleClick = value => {
    setCurrentValue(value)
  }

  const handleMouseOver = newHoverValue => {
    setHoverValue(newHoverValue)
  };

  const handleMouseLeave = () => {
    setHoverValue(undefined)
  }

  function gnext6(){
    axios
        .post("/feed/", {

           
            id: props.location.state.id,
            rating: starValue,
            comment: feed1,

        },
        {
          headers: { 'X-CSRFToken': csrftoken }
      })
        .then((res) => {
            setfeed1('')
            toast({
              title: "Feedback Has Been Submitted",
              description: "You can go to home page now to search for more helplines",
              status: "success",
              duration: 9000,
              isClosable: true,
              position:'top',
          })
          
        })
        .catch((err) => {});
  }

  return (
    <div style={styles.container}>
      <Accordion allowMultiple >
  <AccordionItem fontSize="20px" >
    <h2>
      <AccordionButton>
        <Box flex="1" textAlign="center" fontSize="20px">
        Helpline no : {props.location.state.number}
        </Box>
        <AccordionIcon />
      </AccordionButton>
    </h2>
    <AccordionPanel pb={1}>
      <Text className="Feedback_Media" fontSize="15px" textAlign="left">
<div><strong> Organization Name :</strong> {props.location.state.name} </div>
<div><strong>Organization Type:</strong> {props.location.state.orgtype} </div>
<div><strong>State :</strong> {props.location.state.thestate}</div>
<div><strong>Issues :</strong> {props.location.state.issues.map((item)=>(
  <p>{item},</p>

))} </div>

</Text>

    </AccordionPanel>
  </AccordionItem>
  </Accordion>
      
      <h2 style={{fontSize:"45px"}}> Feedback </h2>
      <div style={styles.stars}>
        {stars.map((_, index) => {
          return (
            <FaStar
              key={index}

              size={28}

              onClick={() =>{ handleClick(index + 1);setstarValue(index + 1)}}
              onMouseOver={() => handleMouseOver(index + 1)}
              onMouseLeave={handleMouseLeave}
              color={(hoverValue || currentValue) > index ? colors.orange : colors.grey}
              style={{
                marginRight: 10,
                cursor: "pointer"
              }}
            />
          )
        })}
      </div>
      <textarea value={feed1} onChange={({target})=>setfeed1(target.value)}
        placeholder="Submit Your Feedback...."
        style={styles.textarea}
      />
    <Button onClick={gnext6}  bg="white" variant="outline" mb={5} _hover={{ bg:"teal" , color:"white"}}>
                Feedback
                
            </Button>

     

    </div>
  );
};


const styles = {
  container: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    
  },
  stars: {
    display: "flex",
    flexDirection: "row",
  },
  textarea: {
    border: "0.1px solid #663300",
    borderRadius: 20,
    padding: 25,
    margin: "20px 0",
    minHeight: 250,
    width: 250,
    outline:"none"
    
  },


};




export default Messages;