import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link,useHistory,NavLink,useLocation,Redirect } from "react-router-dom";
import { Button, Text,Heading, Box, Input,FormControl,FormLabel,InputLeftAddon,InputGroup,useBreakpointValue } from "@chakra-ui/react"
import Cookies from 'js-cookie'

const csrftoken = Cookies.get('csrftoken')
const Home76 = (props) => {
  const [users, setUser] = useState([]);
  const history = useHistory();
  const Location = useLocation();
  useEffect(() => {
    loadUsers();
  }, []);
  

  const loadUsers = async () => {
   
     
    
    setUser(Location.state.id);
  };

  // const deleteUser = async id => {
  //   await axios.delete(`http://localhost:3002/users/${id}`);
  //   loadUsers();
  // }; 
  

  // axios.get("http://localhost:8000/wel/")
  // .then((res) => {
  //   data = res.data
  //   this.setState({
      

  //   });
  // })
  function edit(e){
    axios.post("/abc/",{
      id:e.target.value
      
    },
    {
      headers: { 'X-CSRFToken': csrftoken }
  }).then((res)=>{
      console.log(res.data)
     
    
        history.push({pathname:"/update",
        state:{
          item: res.data,
          id: Location.state.id
        }
      })
      
    })
    
  
  }

  return (
    <><nav class="navbar navbar-expand-lg navbar-light bg-light">
    <a class="navbar-brand" >Intern Panel</a>
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>

    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav mr-auto">
        <li class="nav-item active">
        <NavLink className="nav-link" exact to="/dashboard" >
              Home
            </NavLink>
        </li>
        <li class="nav-item">
        <button className="nav-link" onClick={()=>{history.push({pathname: '/notes',
            state: {
                id:Location.state.id
             }});}}>
              Contact
            </button>
        </li>

      </ul>
      <form class="form-inline my-2 my-lg-0">

        <button type="button" class="btn btn-danger" data-toggle="modal" data-target="#exampleModal">
          Logout
        </button>
        <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="exampleModalLabel">Logout</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                Are you sure want to logout?
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
                <button type="button" onClick={()=>{

                axios.post("/logo/",{
                  id:Location.state.id
                },
                {
                  headers: { 'X-CSRFToken': csrftoken }
              }).then((res)=>{                  
                  window.location.replace("/login");
                  }
                )

                }} class="btn btn-primary">Logout</button>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  </nav>
    <div>

    <Heading textAlign="center">Dashboard</Heading>
      <div className="py-4">
        
        <div class="table table-striped table-responsive-sm">
          <table class="table">
            <thead class="thead-dark">
              <tr>
                <th scope="col">Helpline ID</th>
                <th scope="col">Organization Name</th>
                <th scope="col">contact No.</th>
                <th scope="col">Email</th>
                <th>Action</th>
                <th>Status </th>



              </tr>
            </thead>
            <tbody>
              {users.map((item) => (
                <tr>
                  <th scope="row">{item.id}</th>
                  <td>{item.OrganizationName}</td>
                  <td>{item.PhoneNumber[0]}</td>
                  <td>{item.Email}</td>
                  <td>
                    <div class="d-grid gap-10 col-15">
                      <Button id="edit" value ={item.id} onClick={edit}>
                        Edit
                      </Button>

                    </div>
                  </td>
                  <td>
                 <div class="dot" style={{backgroundColor:item.VerificationStatus}}></div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          
          
        </div>
        
      </div>
    </div></>
  );
};

export default Home76;