import React from 'react'
import {useState} from 'react';
import { Wrap,WrapItem,Radio, RadioGroup, Stack, Checkbox,Grid, Button,Heading,useBreakpointValue} from "@chakra-ui/react";
import aa from './aa.png';

import setting1 from './setting1.png';
import {useHistory} from "react-router-dom";
import { tableRowClasses } from '@mui/material';
import { chakra } from "@chakra-ui/react"

import axios from "axios";
import Cookies from 'js-cookie'

const csrftoken = Cookies.get('csrftoken')


const track = []

function Main_Home(props) {
    const history = useHistory();

    const [key,setkey] = useState([])

    React.useEffect(() => {
        axios.get("/wel/").then((res) => {
        setkey(res.data.issues)
        
        })
      }, []); 
    
    function Click(e){



        if(e.target.style.color=="red"){

            e.target.style.color="black";
            e.target.style.backgroundColor = 'white';
            track.splice(track.indexOf(e.target.value),1);

        }
        else if(e.target.style.color=="black"){
            e.target.style.color = "red";
            e.target.style.backgroundColor="white";
            track.push(e.target.value);

        }
        else{
            track.push(e.target.value);
            e.target.style.color = 'red';
            e.target.style.backgroundColor = 'white';



        }
      }

      



const buttonSize = useBreakpointValue(['sm', 'lg'])
    var a=1
    const [isActive, setActive] = useState("false");

    function gnext1(){
        axios
        .post("/wel/", {

           
            issues: track,
            location:[props.s,props.c],

        },
        {
            headers: { 'X-CSRFToken': csrftoken }
        })
        .then((res) => {
            history.push({pathname: '/post-page',
            state: {
                tracked:track,
                issues: key,
                helplines: res.data,
                price: "All",
                type: "All"
             }});
          
        })
        .catch((err) => {});
        

    }
   
    return (
        <div>
                <div className="home2">
                <div class="wrapper-parent" style={{justifyContent:"center"}}>
            <Heading className="text" fontSize={{ base: "29.5px", sm: "40px", md: "50px"  ,lg: "50px", xl: "50px" }}>
            Womenhelpline.in
            </Heading>
            <div class="colors-container">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
            </div>
                </div>
            
            <div className="Main_between" style={{marginLeft:"auto",justifyContent:"center"}}>
                <div className="between" style={{position:"relative", display:'flex', marginTop:"200px",marginLeft:"auto",justifyContent:"center"}}>
                
                    <Heading style={{color:"black"}} fontSize={{ base: "25px", sm: "30px", md: "35px"  ,lg: "40px", xl: "50px" }} textAlign="center">What would you like help with?</Heading>
                </div>
            

            <div style={{marginTop:"4%",justifyContent:"center",width:"100%"}} >
            {/* <Grid className="MyGrid_GetHelp" id="MyGrid_GetHelp" templateColumns={{base: "repeat(2 , 1fr)", sm: "repeat(2 , 1fr)", md: "repeat(4 , 1fr)"  ,lg: "repeat(4 , 1fr)", xl:"repeat(6 , 1fr)"}}    gap={6}>
            {allbuts1} 

            
            </Grid> */}
            <Wrap justify="center" className="wrap">
                
             {key.map((item)=>(
                
                 <WrapItem> 
                 <Button value={item} variant="outline" onClick={Click}>
                    {item}
                </Button>
                </WrapItem>

             ))}
            
            </Wrap>

                                                                                                                
            </div>
            </div>
            <div style={{width:"100%"}} align="center">
            {/* <Button onClick = {()=>{ data.splice(0,1);$("#MyGrid_GetHelp").empty(); for (var i=0;i<data.length;i++){
                
               var a =  document.createElement("chakra.button");

                a.classList.add("css-1r1mh8u");
                a.innerHTML=data[i];
                document.getElementById("MyGrid_GetHelp").appendChild(a);
            }}}  */}
            <Button
            className="go" onClick={gnext1} backgroundColor="#589c48" color="white" _hover={{border:"3px solid #589c48",backgroundColor:"transparent", color:"#589c48"}} size={buttonSize}  mb={"20%"} mt={"5%"}>
                Search For Helplines
            </Button>
            </div>
           
        </div>    
        </div>
    )
}

export default Main_Home;
     
