import React,{useState} from 'react';
import {Wrap,WrapItem, Grid, useBreakpointValue, GridItem, Flex, Heading, Text, Stack, Button, BeatLoader, Drawer, DrawerOverlay, DrawerContent, DrawerHeader, DrawerBody, DrawerCloseButton, DrawerFooter } from "@chakra-ui/react"
import ReactDOM from 'react-dom';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { AiOutlineCopy } from "react-icons/ai";
import { useToast,AlertDialog,AlertDialogCloseButton,AlertDialogBody,AlertDialogHeader,AlertDialogFooter,AlertDialogContent,AlertDialogOverlay } from "@chakra-ui/react"

import { Input, InputGroup, InputRightAddon, Icon, InputLeftElement, InputRightElement, HStack, Menu, MenuButton, MenuList, MenuOptionGroup, MenuItemOption, MenuDivider, Box, Checkbox, MenuItem } from "@chakra-ui/react"
import { SearchIcon } from "@chakra-ui/icons"

import {useHistory,useLocation} from "react-router-dom";


import { extendTheme } from "@chakra-ui/react"
import filter from './filter.png';
import language from './language.jpg';

import { TriangleDownIcon } from "@chakra-ui/icons"

import { Fade, ScaleFade, Slide, SlideFade } from "@chakra-ui/react"
import { useDisclosure } from "@chakra-ui/react"

import useCollapse from "react-collapsed";
import Cookies from 'js-cookie'


import axios from "axios";
const csrftoken = Cookies.get('csrftoken')

function useForceUpdate(){
  const [value, setValue] = useState(0); // integer state
  return () => setValue(value => value + 1); // update the state to force render
}


function Click(e){


    
  if(e.target.style.color=="red"){

      e.target.style.color="black";
      e.target.style.backgroundColor = 'white';
      track.splice(track.indexOf(e.target.value),1);

  }
  else if(e.target.style.color=="black"){
      e.target.style.color = "red";
      e.target.style.backgroundColor="white";
      track.push(e.target.value);

  }
  else{
      track.push(e.target.value);
      e.target.style.color = 'red';
      e.target.style.backgroundColor = 'white';
      


  }
}



let track = []
let track2 = []
let roww =[]
let helplines= []
let cv = true
let a1,a2
function test(){
    roww =[]
for (var i=0;i<track2.length;i++){

    if(track.includes(track2[i])){
        roww.push(<Button value={track2[i]} onClick={Click}  size="md"  style={{color:'red',fontSize:'10px!important'}}  variant="outline">
                    {track2[i]}
                </Button>)
    }
    else{
        roww.push(
            <Button onClick={Click} value={track2[i]} size="md"  style={{color:'black'}}  variant="outline">
                {track2[i]}
            </Button>
        )
    }
}
}



function Post(props,isActive) {
  const forceUpdate = useForceUpdate();

  const history = useHistory();
  const [sres,setsres] = useState(true)

  React.useEffect(()=>{
    axios
    .get("/wel/")
        .then((res) => {
          track2 = res.data.issues

            }
          
        )  
   
    helplines = props.location.state?props.location.state.helplines:[]
    track = props.location.state?props.location.state.tracked:[]
    if(helplines.length == 0){
      setsres(false)
    }
    else{
      setsres(true)
    }
    
     a1 = props.location.state?props.location.state.price:("All")
   
    a2 = props.location.state?props.location.state.type:("All")
  
})



  const { 
    isOpen: isOpenA, 
    onOpen: onOpenA, 
    onClose: onCloseA 
} = useDisclosure()
  const cancelRef = React.useRef()

  const { 
    isOpen: isOpenB, 
    onOpen: onOpenB, 
    onClose: onCloseB 
} = useDisclosure()
  const cancelRefB = React.useRef()

  
const [value, setValue] = useState("All");
const [value1, setValue1] = useState("All");


  const [isExpanded, setExpanded] = React.useState(isActive);
  const { getToggleProps, getCollapseProps } = useCollapse({

    duration: 1200,
    collapsedHeight: 340
  });

  React.useEffect(() => {
    setExpanded(isActive);
  }, [isActive, setExpanded]);




  


  const { isOpen, onOpen, onClose } = useDisclosure()
  const btnRef = React.useRef()


  function reset(){
    var a=document.getElementById("grd").getElementsByTagName("Button");
    
    for(var i=0;i<a.length;i++){
      var e=a[i]
      if(e.style.color=="red"){
        e.click()
        
    }
    }
  }

function gnext2(e){
  for(var i=0;i<helplines.length;i++){
    if(helplines[i].id ==e.target.value){
      history.push({pathname: '/feedback',
            state: {
                number : helplines[i].number[0],
                name : helplines[i].orgname,
                orgtype : helplines[i].orgtype,
                thestate :helplines[i].state,
                issues : helplines[i].issues,
                id : helplines[i].id
             }});

    }
  }

}
function gnext3(){
  
  axios
        .post("/post/", {

           
            issues: track,
            location:[props.s,props.c],
            price: value,
            orgtype: value1


        },
        {
          headers: { 'X-CSRFToken': csrftoken }
      })
        .then((res) => {
         history.push({pathname: '/post-page',
            state: {
                tracked:track,
                helplines: res.data,
                price: value,
                type: value1
             }});
            })
            .catch((err) => {});
}


 function yeah(){
   if(cv){
    setValue(a1);
    setValue1(a2);
    cv = false
   }

 }
  const toast = useToast()
  return (


    <><div>

      <div className="Filterss">



                


        <Button ref={btnRef} colorScheme="teal" onClick={()=>{test();onOpen()}}>
          Issues
        </Button>
        <Drawer
          isOpen={isOpen}
          placement="right"
          onClose={onClose}
          trapFocus={true}
          
          

          size={"sm"}


        >
          <DrawerOverlay />
          <DrawerContent>
            <DrawerCloseButton />
            <DrawerHeader>Select Issue...</DrawerHeader>

            <DrawerBody>
              <Grid className="grd" id="grd" templateColumns={{base:"repeat(1, 1fr)" ,sm:"repeat(2, 1fr)" }} gap={6}>

                {roww}


              </Grid>
            </DrawerBody>

            <DrawerFooter>

           
              <Button style={{marginRight:"20px"}} colorScheme="blue" onClick={gnext3} >Save</Button>
              <Button colorScheme="blue" onClick={onOpenA}>Reset</Button>


      <AlertDialog
        motionPreset="slideInBottom"
        leastDestructiveRef={cancelRef}
        onClose={onCloseA}
        isOpen={isOpenA}
        isCentered
      >
        <AlertDialogOverlay />

        <AlertDialogContent>
          <AlertDialogHeader>Reset</AlertDialogHeader>
          <AlertDialogCloseButton />
          <AlertDialogBody>
            Are you sure 
          </AlertDialogBody>
          <AlertDialogFooter>
            <Button ref={cancelRef} onClick={onCloseA}>
              No
            </Button>
            <Button colorScheme="teal" onClick={()=>{reset();onCloseA()}} ml={3}>
              Yes
            </Button>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

            </DrawerFooter>
          </DrawerContent>
        </Drawer>








        {/*------------More Filters--------------*/}
        <Menu closeOnSelect={false}>
                  <MenuButton as={Button} ml="30px" sx={{ backgroundColor: 'white', border: '1px solid' }} onClick={yeah} rightIcon={<img className="filter-image" src={filter} alt="Logo" />}>

                    More filters
                  </MenuButton>
                  <MenuList minWidth="240px">
                    <MenuOptionGroup value={value} onChange={(opt)=> {setValue(opt)}} title="Order" type="radio">
                    <MenuItemOption value="All">All</MenuItemOption>
                      <MenuItemOption value="Free">Free</MenuItemOption>
                      <MenuItemOption value="Paid">Paid</MenuItemOption>
                    </MenuOptionGroup>
                    <MenuDivider />
                    <MenuOptionGroup value={value1} onChange={(opt)=> {setValue1(opt)}} title="Type" type="radio" className="myform" >
                      <MenuItemOption value="All">All</MenuItemOption>
                      <MenuItemOption value="Government">Government</MenuItemOption>
                      <MenuItemOption value="Government Funded">Government Funded</MenuItemOption>
                      <MenuItemOption value="Private">Private</MenuItemOption>
                      <Flex style={{width:"100%",justifyContent:"left"}}>
                      <button className="Filter_Reset" style={{marginLeft:"10px"}}  onClick={onOpenB} >Reset</button>
                      </Flex>
        <AlertDialog
        motionPreset="slideInBottom"
        leastDestructiveRef={cancelRefB}
        onClose={onCloseB}
        isOpen={isOpenB}
        isCentered
      >
        <AlertDialogOverlay />

        <AlertDialogContent>
          <AlertDialogHeader>Reset</AlertDialogHeader>
          <AlertDialogCloseButton />
          <AlertDialogBody>
            Are you sure 
          </AlertDialogBody>
          <AlertDialogFooter>
            <Button ref={cancelRefB} onClick={onCloseB}>
              No
            </Button>
            <Button colorScheme="teal" onClick={()=> {setValue("All");setValue1("All");onCloseB()}} ml={3}>
              Yes
            </Button>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
                      {/* <input type="button" onClick={myFunction} value="Reset form"></input> */}
                    </MenuOptionGroup>
                  </MenuList>
                </Menu>
                {/* ()=> {setValue("all");setValue1("all1");} */}
      </div>


    </div>

    
      <div className="post">
        <Grid className="grid" id="card" templateColumns="repeat(3, 1fr)" gap={6} m={10}>
        
        {sres?(
        helplines.map((item)=>(
                <Flex {...getToggleProps({
                  style: { display: "block" },
                  onClick: () => setExpanded((x) => !x)
                })} {...getCollapseProps()} flexDir="column" bg="white" color="black" border="solid black 0.1px" p="5" borderRadius="10px" _hover={{ boxShadow: "0px 8px 15px rgba(0, 0, 0, 0.5)", cursor: "pointer" }}>
                  <Stack spacing={2}>
                    <Flex bg="teal" borderRadius="10px"><Heading as="h1" size="lg" color="white" m="0 auto" p="3">
                      {item.state}
                    </Heading></Flex>
                    <Text textAlign="center">
                     <strong style={{fontSize:'20x',fontFamily:"times new roman"}}> {item.orgname}</strong>
                    </Text>
                    <Text textAlign="center" >
                     {item.date}
                    </Text>
                    <Heading size="md">
                      Helpline Number with STD code:
                    </Heading>
                    <Grid templateColumns="repeat(1, 1fr)" gap={6}>
                      {item.number.map((item1)=>(
                        <Flex style={{width:"100%",justifyContent:"center"}} alignItems="center" bg="white" borderRadius="10px" >
                    
      
                        <Heading  size="lg" paddingLeft=" 0px" color="red" mr={10}>
                          {item1}
                        </Heading>
                        <CopyToClipboard text={item1} onCopy={() => toast({
                          title: "COPIED",
                          description: "Phone Number Copied.",
                          status: "success",
                          duration: 1000,
                          isClosable: true,
                        })}>
                          <AiOutlineCopy style={{ color: "black", fontSize: "20px", cursor: "pointer" }} />
                        </CopyToClipboard>
                        </Flex>

                      ))}
                    </Grid>
      
                    <Text > <strong>Agency Running Helpline:</strong> {item.orgtype} </Text>
                    <Text > <strong>Cost or Charges:</strong> {item.cost} </Text>
                    <Text> <strong>Operational Hours:</strong> {item.ophrs} </Text>
                    <Text> <strong>Location:</strong> {item.city} </Text>
                    <Text> <strong>Email:</strong> {item.email} </Text>
                    <Text> <strong>Website:</strong> {item.url} </Text>
                    <Text>
                      {item.description}
                    </Text>
                    <Text ><strong> HLID:</strong>{item.id} </Text>
                    <Wrap>
                    {item.issues.map((item2)=>(
                
                        <WrapItem> 
                        <Button colorScheme="blue">
                          {item2}
                      </Button>
                      </WrapItem>

                    ))}
                    </Wrap>
                    <Button onClick={gnext2} value={item.id} className="feeds" style={{ backgroundColor: "teal", color: "white" }} >
                      Feedback
                    </Button>
                  </Stack>
                </Flex>
            ))):(
              <>
             <Box>
              <Heading as="h1" size="lg" width="100%" style={{justifyContent:"center"}}>
                      No Helplines Found
                    </Heading><br></br>
              <h1><strong><em>Go to issues and Click on Save</em></strong></h1>
                   </Box>
              </>
            )}
        
        </Grid>
        
      </div>
    </>
  )
}

export default Post;



