import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Navbar from './components/Navbar';
import './App.scss';

import './index.css';
import Main_Home from './pages/Main_Home';
import Home from './pages/Home';
import Reports from './pages/Reports';
import Products from './pages/Products';
import Team from './pages/Team';
import Messages from './pages/Messages';
import Footer from './pages/Footer';
import Support from './pages/Support';
import post from './pages/post-page';
import Login from './pages/admin';
import NotFound from './pages/NotFound';

import Home76 from './pages/table';
import Home77 from './pages/update';
import Contact from './pages/Contact';

import * as React from "react"
import { ChakraProvider } from "@chakra-ui/react"
class App extends React.Component {
  render(){
  return (
    <>

<Router>
        <ChakraProvider>
          <Switch>
            <Route path="/login" exact component={Login} />
            <Route path="/dashboard" exact component={Home76} />
            <Route path="/update" exact component={Home77} />
            <Route path="/notes" exact component={Contact} />
           
            <div>
              <Navbar />
              


              <Footer />
            </div>


          </Switch>




        </ChakraProvider>


      </Router>

    </>
  );
}}

export default App;




