import React, { useState } from 'react'
      import * as FaIcons from "react-icons/fa";
      import * as AiIcons from "react-icons/ai";
      import { Link } from 'react-router-dom'
      import { SidebarData } from './SidebarData'
      import './Navbar.scss';
      import { IconContext } from 'react-icons';

      import { Dropdown } from 'semantic-ui-react'
      import location from './location.png';
      import MultiSelect from  'react-multiple-select-dropdown-lite'
      import  'react-multiple-select-dropdown-lite/dist/index.css';
      import { CssBaseline, Container } from "@material-ui/core/";
      import { makeStyles } from "@material-ui/core/styles";
  
      import axios from "axios";

      
      import { Button,Select,Text } from "@chakra-ui/react"
      import {Input,useBreakpointValue,FormControl,FormLabel,value,options,Stack ,InputGroup,InputRightAddon, Icon,InputLeftElement,InputRightElement,HStack, Menu, MenuButton, MenuList, Grid, MenuOptionGroup, MenuItemOption, MenuDivider ,Flex, Box ,Checkbox,MenuItem} from "@chakra-ui/react"
      import { SearchIcon } from "@chakra-ui/icons"
      

      import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

      import { extendTheme } from "@chakra-ui/react"

      
      import { TriangleDownIcon } from "@chakra-ui/icons"
      
      import { Fade, ScaleFade, Slide, SlideFade } from "@chakra-ui/react"
      import {
        Modal,
        ModalOverlay,
        ModalContent,
        ModalHeader,
        ModalFooter,
        ModalBody,
        ModalCloseButton,
      } from "@chakra-ui/react"
      import { useDisclosure } from "@chakra-ui/react"

//
import Main_Home from '../pages/Main_Home';
import Home from '../pages/Home';
import Reports from '../pages/Reports';
import Products from '../pages/Products';
import Team from '../pages/Team';
import Messages from '../pages/Messages';
import Support from '../pages/Support';
import Post from '../pages/post-page';
import NotFound from '../pages/NotFound';
//

import {useHistory} from "react-router-dom";


      const useStyles = makeStyles(theme => ({
        form: {
          width: "100%", // Fix IE 11 issue.
          marginTop: theme.spacing(3)
        }
      }));

      const states1 = []
      const data1 = []
      axios
        .get("/wel/")
        .then((res) => {
            data1.push(res.data);
            const key = Object.keys(data1[0].location);
            states1.push(<option value="All States">All States</option>);
            for(var i=0;i<key.length;i++){

              states1.push(<option value={key[i]}>{key[i]}</option>)

            }
          
        })  
        .catch((err) => {});
      
function Navbar() {
  const history = useHistory();


  const classes = useStyles();
    const [state, setState] = useState("All States");
    const [city, setCity] = useState("All Cities");

    function dothis(e){
      let curv = e.target.selectedOptions[0].value;
      if( curv == "All States"){

        setState("All States")
        setCity("All Cities")
        return ["All Cities"]
        

      }
      else if(curv == "")
      {

        return ['Select City']
      }
      else{
        setState(e.target.selectedOptions[0].value)
      return data1[0].location[curv]
    }
     

    }
  
  const { isOpen, onOpen, onClose } = useDisclosure()

        const [details, setDetails] = useState(null);

    

    const buttonSize = useBreakpointValue(['xs', 'md'])
      

      
      
        const [sidebar, setSidebar] = useState(false)
      
        const showSidebar = () => setSidebar(!sidebar)
      
      
        const [myOptions, setMyOptions] = useState([])
      
      
      

      
      
      
            
          
      
        return (
          <><IconContext.Provider value={{  }}>
            
            
            <div  className="navbar69">



               
              <button className='menu-bars' onClick={showSidebar} >
              <FaIcons.FaBars  style={{cursor:"pointer"}}  />
              </button>

              
              <HStack sx={{ marginLeft: '4%' }}>
              {/* <Button >Open Modal</Button> */}
              <Button onClick={onOpen}  className="But" sx={{ paddingRight: '30px', paddingLeft: '20px' }}  fontSize={["36%","50%","50%","80%"]}>
                  <img src={location} alt="Logo" />
                  &nbsp;Location
                </Button>
                
                <Modal isOpen={isOpen} onClose={onClose}>
                  <ModalOverlay />
                  <ModalContent>
                    <ModalHeader>Location</ModalHeader>
                    <ModalCloseButton onClick={onClose} />
                    <ModalBody>
                      

            {/* <Input mt={"10%"} style={{color:"black"}} variant="flushed" placeholder="Search for any location..." />   */}
            <FormControl id="first-name" isRequired>
                <FormLabel mt={"1%"} size="xl" >Set a Location</FormLabel>
                {value}
          

                <Container component="main" >

<CssBaseline />
<div className={classes.paper}>
  <form className={classes.form}>

    <Grid container spacing={2}>

      <Grid item xs={12} sm={4}>
        
      <Select onChange={(e)=>{document.getElementById('napple').innerHTML="";const data4 = dothis(e);
      if(data4[0] != "Select City"){
      var but1 = document.createElement('option');
      but1.value = 'All Cities';
      but1.innerHTML = 'All Cities';
      document.getElementById('napple').appendChild(but1);
      }
      for(var i=0;i<data4.length;i++){
        var but1 = document.createElement('option');
        but1.value = data4[i];
        but1.innerHTML = data4[i];
        document.getElementById('napple').appendChild(but1);
      }}} className="apple" placeholder='Select State'>
       {states1}
      </Select>
        
      </Grid>

      <Grid item xs={12} sm={4}>

      <Select className="napple" id="napple" onChange={(e)=>setCity(e.target.selectedOptions[0].value)} placeholder='Select City'>
     
      </Select>
      </Grid>

    </Grid>

  </form>
</div>
</Container>
                </FormControl>         
                        

                    </ModalBody>

                    <ModalFooter>
                      
                      <Button colorScheme="teal" onClick={()=>{document.getElementsByClassName('But')[0].innerHTML=state+"/"+city;onClose()}} variant="solid">Confirm Location</Button>
                    </ModalFooter>
                  </ModalContent>
                </Modal>
                
                
              </HStack>

            <Flex>

              <Button className="needhelp" onClick={()=>{history.push('/gethelp')}} type="button"><div id="nh">Need Help?</div></Button>
            <Button className="submithelpline" onClick={()=>{history.push('/submithl')}} type="button"><div id="nh">Submit a Helpline</div></Button>

            <div id="google_element" ></div>                
                                 
            </Flex>

            </div>
            <nav className={sidebar ? 'nav-menu active' : 'nav-menu'}>
              <ul className="nav-menu-items" onClick={showSidebar}>
                <li className="navbar-toggle">
                      <button className="menu-bars">
                        <AiIcons.AiOutlineClose style={{cursor:"pointer"}} />
                      </button>
                </li>
                {SidebarData.map((item, index) => {
                  return (
                    <li key={index} className={item.cName}>
                      <Link to={item.path}>
                        {item.icon}
                        <span>{item.title}</span>
                      </Link>
                    </li>
                  );
                })}
              </ul>
            </nav>
            <Switch>
                <Route path="/submithl" exact component={Main_Home} />
                <Route path="/" exact render={(props) => <Home s={state} c={city} {...props} /> }  />
                <Route path="/gethelp" component={Products} />
                <Route path="/feedback" component={Messages} />
                <Route path="/faq" component={Support} />
                <Route path="/post-page" render={(props) => <Post s={state} c={city} {...props} /> } />

                <Route component={NotFound} />
              </Switch>


            



          </IconContext.Provider></>
      
      
      
      
      
          
      
      
      
      
      
      
        )
      }
      
      
      
      
      
      
      
      export default Navbar