import React, { useState,useEffect } from 'react'
import MultiSelect from  'react-multiple-select-dropdown-lite'
import  'react-multiple-select-dropdown-lite/dist/index.css';
import { Button, Text,Heading, Box, Input,FormControl,FormLabel,InputLeftAddon,InputGroup,useBreakpointValue,useToast, Textarea,Stack, Radio,RadioGroup } from "@chakra-ui/react"
import { CCard,CCardTitle,CCardHeader,CCardBody,CCardFooter,CCardText,CButton } from "@coreui/react";
import Select from 'react-select';
import 'react-multiple-select-dropdown-lite/dist/index.css';
import axios from 'axios';
import { Link,useHistory,NavLink } from "react-router-dom";
import { TextField } from '@material-ui/core';
import Cookies from 'js-cookie'

const csrftoken = Cookies.get('csrftoken')

function Home77(props) {
    
    const history = useHistory()
        
        const [name, setname] = useState(props.location.state.item.OrganizationName);
        const [address, setaddress] = useState(props.location.state.item.OrganizationAddress);
        const [phone, setphone] = useState(props.location.state.item.PhoneNumber[0]+","+props.location.state.item.PhoneNumber[1]);
        const [issues, setissue] = useState(props.location.state.item.Issues);
       
        const [disc,setdisc] = useState(props.location.state.item.OrganizationDescription);
        const [type,settype] = useState(props.location.state.item.OrganizationType);
        const [email,setemail] = useState(props.location.state.item.Email);
        const [web,setweb] = useState(props.location.state.item.Website);
        const [hours,sethours] = useState(props.location.state.item.OperationalHours);
        
       const [iv,setiv] = useState(props.location.state.item.isvalid?"True":"False")
        
       
        const toast = useToast()
        const positions = [
            "top"
        ]
      
    
    
        
    
        const [state, setState] = useState(props.location.state.item.State);
        const [city, setCity] = useState(props.location.state.item.City);
    


        

    

    const buttonSize = useBreakpointValue(['md', 'lg'])

    function Click(){
        
    
      console.log(iv)
        axios.post("/subd/",{
            OrgName:name ,
            orgadd:address,
            phone:phone,
            city:city,
            state:state,
            issues:issues,
            dis:disc,
            type:type,
            web:web,
            hours:hours,
            email:email,
            valid:iv,
            id:props.location.state.item.id,
        },
        {
          headers: { 'X-CSRFToken': csrftoken }
      }).then((res)=>{
            
            
            })
            
        

       

       
            document.getElementById("orgname").value = "";
            setname("");
            document.getElementById("orgaddress").value = "";
            setaddress("");
            document.getElementById("state").value = "";
            setState("");
            document.getElementById("city").value = "";
            setCity("");
            document.getElementById("subphone").value = "";
            setphone("");
            document.getElementById("subissues").value = "";
            setissue("");
            document.getElementById("description").value = "";
            setdisc("");
            document.getElementById("email").value = "";
            setemail("");
           
            document.getElementById("hours").value = "";
            sethours("");
           
            document.getElementById("type").value = "";
            settype("");
            
                
            history.push({pathname: '/dashboard',
            state: {
                id:props.location.state.id
             }});





        
    }


    

    


   

    return (
        <><nav class="navbar navbar-expand-lg navbar-light bg-light">
        <a class="navbar-brand" >Intern Panel</a>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto">
          <li class="nav-item active">
          <button className="nav-link" onClick={()=>{history.push({pathname: '/dashboard',
            state: {
                id:props.location.state.id
             }});}}>
                Home
              </button>
          </li>

        </ul>
        <form class="form-inline my-2 my-lg-0">

          <button type="button" class="btn btn-danger" data-toggle="modal" data-target="#exampleModal">
            Logout
          </button>
          <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
              <div class="modal-content">
                <div class="modal-header">
                  <h5 class="modal-title" id="exampleModalLabel">Logout</h5>
                  <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div class="modal-body">
                  Are you sure want to logout?
                </div>
                <div class="modal-footer">
                  <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
                  <button type="button" class="btn btn-primary"onClick={()=>{

axios.post("/logo/",{
  id:props.location.state.id
},
{
  headers: { 'X-CSRFToken': csrftoken }
}).then((res)=>{                  
  window.location.replace("/login");
  }
)

}}>Logout</button>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </nav>
    
        <div className="home">
            <Box >
            <Heading mt={10} style={{textAlign :"center"}} fontSize={{ base: "29px", sm: "35px", md: "40px"  ,lg: "45px", xl: "50px"}} mb={30}>Verify Helpline</Heading>
            
            
            <div style={{paddingLeft: "20%", paddingRight: "20%" }} className="Sput">

               {/* {users.map((item)=>  */}

            <FormLabel >Is it a Valid Helpline?</FormLabel>   
            
            <RadioGroup  defaultValue={iv} >
            <Stack >
            <Radio colorScheme="green" onChange={({target}) => setiv(target.value)}value="True">
                    Yes
                </Radio>
                <Radio colorScheme="red" onChange={({target}) => setiv(target.value)}value="False">
                    No               
                </Radio>
                
            </Stack>
            </RadioGroup> 
           
        

            <FormControl style={{paddingBottom:"20px"}}  id="first-name" >
            <FormLabel>Organization Name</FormLabel>
            <Input value={name} onChange={({target}) => setname(target.value)} id="orgname" placeholder="Organization Name"  />
            {/* <input type="button" onClick={myFunction} value="Reset form"></input> */}
            </FormControl>
            <FormControl style={{paddingBottom:"20px"}}  id="first-name" >
            <FormLabel >Address of the organization</FormLabel>
            <Input value={address} onChange={({target}) => setaddress(target.value)} id="orgaddress" placeholder="Organization Name" />
            </FormControl>
            <FormControl style={{paddingBottom:"20px"}} id="first-name" >
            <FormLabel >Contact Numbers</FormLabel>
            <InputGroup>
            <Textarea type="tel" value={phone} onChange={({target}) => setphone(target.value)} id="subphone" placeholder="phone number" />
            </InputGroup>
            </FormControl>
            <FormControl style={{paddingBottom:"20px"}}  id="first-name" >
            <FormLabel >City</FormLabel>
            <Input value={city} onChange={({target}) => setCity(target.value)} id="city" placeholder="City" />
            </FormControl>
            <FormControl style={{paddingBottom:"20px"}}  id="first-name" >
            <FormLabel >State</FormLabel>
            <Input value={state} onChange={({target}) => setState(target.value)} id="state" placeholder="State" />
            </FormControl>
            <FormControl style={{paddingBottom:"20px"}}  id="first-name" >
            <FormLabel >Issues</FormLabel>
            <Input id="subissues" value={issues} onChange={({target}) => setissue(target.value)} placeholder="Issues" />
            </FormControl>
            <FormControl style={{paddingBottom:"20px"}}  id="first-name" >
            <FormLabel >Organiztion Description</FormLabel>
            <Textarea value={disc} onChange={({target}) => setdisc(target.value)} id="description" placeholder="Description" />
            </FormControl>
            <FormControl style={{paddingBottom:"20px"}}  id="first-name" >
            <FormLabel >Organization Type</FormLabel>
            <Input value={type} onChange={({target}) => settype(target.value)} id="type" placeholder="Type" />
            </FormControl>
            <FormControl style={{paddingBottom:"20px"}}  id="first-name" >
            <FormLabel >E-mail</FormLabel>
            <Input value={email} onChange={({target}) => setemail(target.value)} id="email" placeholder="Email" />
            </FormControl>
            <FormControl style={{paddingBottom:"20px"}}  id="first-name" >
            <FormLabel >Website</FormLabel>
            <Input value={web} onChange={({target}) => setweb(target.value)} id="url" placeholder="URL" />
            </FormControl>
            <FormControl style={{paddingBottom:"20px"}}  id="first-name" >
            <FormLabel >Operational Hours</FormLabel>
            <Input value={hours} onChange={({target}) => sethours(target.value)} id="hours" placeholder="Hours" />
            </FormControl>
            
            
            
            
            {/* <FormControl id="first-name" isRequired>
                <FormLabel mb={-30} pb={10}  size="xl" >Issues</FormLabel>
                {value}
           

            <MultiSelect className= "ph" hidePlaceholder={true} placeholder = "search" 
                
                options={options} />
                </FormControl> */}
                    
        </div>
        <div class="submit_SH" style={{marginLeft:"20%",marginBottom:"2%"}}>
            <Button backgroundColor="#589c48" color="white" _hover={{border:"2px solid #589c48",backgroundColor:"transparent", color:"#589c48"}} onClick={Click} size={buttonSize} m="24px 0 24px 0">
                    Verify
            </Button>
            </div>
            
            </Box>
        </div>
    </>
        
    )
}

export default Home77;