import React, { useState } from 'react'
import MultiSelect from  'react-multiple-select-dropdown-lite'
import  'react-multiple-select-dropdown-lite/dist/index.css';
import { useBreakpointValue,Button,chakra, Text,Heading, Box, Input,FormControl,FormLabel,InputLeftAddon,InputGroup, useToast,Grid,classes,NumberInput,NumberInputField} from "@chakra-ui/react"
import Recaptcha from "react-recaptcha";
import Select from 'react-select';
import { stringLiteralTypeAnnotation } from '@babel/types';
import axios from 'axios';
import { height } from '@mui/system';
import  { Component } from 'react';


import { CssBaseline, Container } from "@material-ui/core/";
import { makeStyles } from "@material-ui/core/styles";
import { styled } from '@material-ui/styles';
import Cookies from 'js-cookie'

const csrftoken = Cookies.get('csrftoken')





const useStyles = makeStyles(theme => ({
    form: {
      width: "100%", // Fix IE 11 issue.
      marginTop: theme.spacing(3)
    }
  }));


function Home() {
    const [options,seto] =useState([])
    React.useEffect(() => {
        let l =[]
        axios
        .get("/wel/")
        .then((res) => {
           for(var i=0;i<res.data.issues.length;i++){
               l.push({label: res.data.issues[i],value:i+1})
           }
          seto(l)
        
        })
        .catch((err) => {});
        
      },[]);
   

    const buttonSize = useBreakpointValue(['md', 'lg']);

    const [value, setvalue] = useState('')
    const [name, setname] = useState('');
    const [address, setaddress] = useState('');
    const [phone, setphone] = useState('');
    const [phone2, setphone2] = useState('');
    const [issues, setissue] = useState('');
    const [captcha, setcaptcha] = useState('');
    const [stdcode, setstd] = useState('');
    
    
    const track = [];
    const toast = useToast()
    const positions = [
        "top"
    ]
    const [selectedOption, setSelectedOption] = useState('');

        // handle onChange event of the dropdown
    const handleChange = e => {
            setSelectedOption(e);
    }


    const classes = useStyles();

    const [state, setState] = useState("");
    const [city, setCity] = useState("");

    const [num1, setnum1] = useState("");
    
    const [num3, setnum3] = useState("");


    function Click(){

        var testickle = $('#std').val() + '-' + $('#subphone').val() ;
           
        var testickle1 = $('#call').val() + '-' + $('#subphone2S').val() ;

        var check = false
        var validno =  /^\(?([0-9]{3})\)?[-. ]?([0-9]{3})[-. ]?([0-9]{4})$/
        var validlandline = /^\(?([0-9]{3})\)?[-. ]?([0-9]{4})$/
        var isCaptchaValidated = false;
        var response = grecaptcha.getResponse();
        
        if(validno.test(phone) || validlandline.test(phone)){
            check = false;
        }
        else{
            check = true;
        }

        if(name == ""){

                toast({
                    title: "Invalid Name",
                    description: "Enter a valid organization name",
                    status: "error",
                    duration: 9000,
                    isClosable: true,
                    position:'top',
                })

        }

        else if(address == ""){
            toast({
                title: "Invalid Address",
                description: "Enter a valid address",
                status: "error",
                duration: 9000,
                isClosable: true,
                position:'top',
            })
        }
        else if(state == ""){
            toast({
                title: "Invalid Location",
                description: "Enter a valid State",
                status: "error",
                duration: 9000,
                isClosable: true,
                position:'top',
            })
        }
        else if(city == ""){
            toast({
                title: "Invalid Location",
                description: "Enter a valid City",
                status: "error",
                duration: 9000,
                isClosable: true,
                position:'top',
            })
        }
        else if(check || $('#std').val()==""){
            toast({
                title: "Invalid Number",
                description: "Enter a valid Contact Number",
                status: "error",
                duration: 9000,
                isClosable: true,
                position:'top',
            })

        }
        else if(selectedOption == ''){
            toast({
                title: "Required Field",
                description: "Select following issues",
                status: "error",
                duration: 9000,
                isClosable: true,
                position:'top',
            })
        }
        else if(response.length == 0){
            isCaptchaValidated = false;
            toast({
                title: "Required Field",
                description: "Please verify that you are a human",
                status: "error",
                duration: 9000,
                isClosable: true,
                position:'top',
            })

        }
       
        else{

            for(var i=0; i < selectedOption.length ; i++){
                track.push( selectedOption[i]['label']);
            }
            axios
        .post("/subh/", {
           
            issues: track,
            state: state,
            city: city,
            add: address,
            phone: testickle+","+testickle1,
            name: name,

        },
        {
            headers: { 'X-CSRFToken': csrftoken }
        }
        )
        .then((res) => {
            toast({
                title: "Helpline Submitted",
                description: "We've created a Helpline.",
                status: "success",
                duration: 9000,
                isClosable: true,
                position: 'top',
                })
        })
        .catch((err) => {});
            document.getElementById("orgname").value = "";
            setname("");
            document.getElementById("orgaddress").value = "";
            setaddress("");
            setState("Select Option");
            document.getElementById("district").value = "";
            setCity("");
            document.getElementById("subphone").value = "";
            setphone("");
            document.getElementById("std").value="";
            setnum1("");
            document.getElementById("call").value="";
            setnum3("");
            setphone2("");
            setSelectedOption('')
         
    }
    }

        return (
        <div className="home">

            <Box >
            <Heading mt={10} style={{textAlign :"center"}} fontSize={{ base: "29px", sm: "35px", md: "40px"  ,lg: "45px", xl: "50px"}} mb={30}>Submit A Helpline No.</Heading>

            <div style={{paddingLeft: "20%", paddingRight: "20%" }} className="Sput">



            <FormControl style={{paddingBottom:"20px"}} id="first-name" isRequired>
            <FormLabel mb={-30} pb={3}>Organization Name</FormLabel>
            <Input value={name} onChange={({target}) => setname(target.value)} id="orgname" placeholder="Organization Name" />
            </FormControl>
            <FormControl style={{paddingBottom:"20px"}} id="first-name" isRequired>
            <FormLabel mb={-30} pb={3}>Address of the organization</FormLabel>
            <Input value={address} onChange={({target}) => setaddress(target.value)} id="orgaddress" placeholder="Organization Address" />
            </FormControl>
            
            <FormControl style={{paddingBottom:"0px"}} id="first-name" isRequired>
            <FormLabel  mb={-30} pb={3} >Select State</FormLabel>
           
            
            </FormControl>
            <chakra.select value={state} onChange={({target}) => setState(target.value)} placeholder="Select option" id="ytu" >
            <option value="Select Option">Select Option</option>

            <option value="Andaman and Nicobar Islands">Andaman and Nicobar Islands</option>
  <option value="Andhra Pradesh">Andhra Pradesh</option>
  <option value="Arunachal Pradesh">Arunachal Pradesh</option>
  <option value="Assam">Assam</option>
  <option value="Bihar">Bihar </option>
  <option value="Chandigarh">Chandigarh</option>
  <option value="Chhattisgarh">Chhattisgarh</option>
  <option value="Dadra and Nagar Haveli">Dadra and Nagar Haveli</option>
    <option value="Daman and Diu">Daman and Diu</option>
  <option value="Delhi">Delhi</option>
  <option value="Goa">Goa</option>
  <option value="Gujarat">Gujarat</option>
  <option value="Haryana">Haryana</option>
  <option value="Himachal Pradesh">Himachal Pradesh</option>
  <option value="Jammu and Kashmir">Jammu and Kashmir</option>
  <option value="Jharkhand">Jharkhand </option>
  <option value="Karnataka">Karnataka</option>
  <option value="Kerala">Kerala </option>
   <option value="Lakshadweep">Lakshadweep</option>
  <option value="Madhya Pradesh">Madhya Pradesh</option>
  <option value="Maharashtra">Maharashtra </option>
  <option value="Manipur">Manipur</option>
  <option value="Meghalaya">Meghalaya </option>
  <option value="Mizoram">Mizoram </option>
  <option value="Nagaland">Nagaland</option>
  <option value="Odisha">Odisha</option>
   <option value="Puducherry">Puducherry </option>
  <option value="Punjab">Punjab</option>
  <option value="Rajasthan">Rajasthan</option>
  <option value="Sikkim">Sikkim</option>
  <option value="Tamil Nadu">Tamil Nadu</option>
  <option value="Telangana">Telangana</option>
  <option value="Tripura">Tripura</option>
  <option value="Uttarakhand">Uttarakhand</option>
  <option value="Uttar Pradesh">Uttar Pradesh</option>
  <option value="West Bengal">West Bengal</option>

            </chakra.select>
            <FormControl style={{paddingBottom:"20px",marginTop:"10px"}} id="first-name" isRequired>
            <FormLabel  mb={-30} pb={3} >Select District</FormLabel>
            <Input value={city} onChange={({target}) => setCity(target.value)} id="district" placeholder="Enter District" />
            </FormControl>



               
                
            <FormControl style={{paddingBottom:"20px"}} id="first-name" isRequired>
            <FormLabel  mb={-30} pb={10}>Phone Number</FormLabel>
            <InputGroup>
                <Input value={num1} id="std" onChange={({target})=>setnum1(target.value)} width="100px" placeholder="STD"/>
                    
            {/* <NumberInputField placeholder="+91/022" style={{fontSize:"13.5px"}} width="100px"/> */}

            
            <Input type="tel" value={phone} onChange={({target}) => setphone(target.value)} id="subphone" placeholder="phone number" />
            </InputGroup>
            {/* <Input type="tel" value={phone} onChange={({target}) => setphone(target.value)} id="subphone" placeholder="phone number" /> */}
            
            </FormControl>
            <FormControl style={{paddingBottom:"20px"}} id="first-name" >
            <FormLabel  mb={-30} pb={10}>Alternate Phone Number</FormLabel>
            <InputGroup>
            <Input id="call" value={num3} onChange={({target})=>setnum3(target.value)}  width="100px" placeholder="STD" />
            {/* <NumberInputField placeholder="+91/022" style={{fontSize:"13.5px"}} width="100px"/> */}           
            <Input type="tel" value={phone2} onChange={({target}) => setphone2(target.value)} id="subphone2S" placeholder="phone number" />
            </InputGroup>
            </FormControl>



            <FormLabel style={{ paddingBottom:"20px"}} fontSize={{ base: "20px", sm: "20px", md: "20px"  ,lg: "20px", xl: "20px"}} mb={0} pb={3}>Issues</FormLabel>
                    <div className="dropdown">


                        <Select options={options}
                        isMulti
                        placeholder="Issues"
                        value={selectedOption}

                        onChange={handleChange} // assign onChange function
                        id="subissues"

                         />


                        
                         {selectedOption && <div style={{ marginTop: 20, lineHeight: '25px' }}>
                            
                        </div>}
                    </div>

                    <div class="g-recaptcha" data-sitekey="6LcfWD4dAAAAABD-E1Uhdur3PDCCmSx0XWS52N6N" data-callback="verifyCaptcha"></div>
    <div id="g-recaptcha-error"></div>

            </div>
            
           
                
                <div class="submit_SH" style={{marginLeft:"20%",marginBottom:"2%"}}>
                <Button  backgroundColor="#589c48" color="white" _hover={{border:"2px solid #589c48",backgroundColor:"transparent", color:"#589c48"}} size={buttonSize} m="24px 0 24px 0"
                 onClick={Click}
                >
                    Submit
                </Button>
                </div>

            </Box>

            
        </div>

    )
}

export default Home;